input#switch[type="checkbox"] {
  visibility: hidden;
  width: 0;
  height: 0;
}

.h-switch {
  cursor: pointer;
  text-indent: -9999px;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border-radius: 10px;
  width: 40px;
  height: 20px;
  display: block;
  position: relative;
  box-shadow: inset 1px 1px 2px #0000001a;
}

.h-switch:after {
  content: "";
  background: #fff;
  border-radius: 50%;
  width: 18px;
  height: 18px;
  transition: all 1s;
  position: absolute;
  top: 1px;
  left: 1px;
}

input#switch:checked + .h-switch {
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
  box-shadow: inset 1px 1px 2px #0000001a;
}

input#switch:checked + .h-switch:after {
  left: calc(100% - 1px);
  transform: translateX(-100%);
}

.h-switch:active:after {
  width: 18px;
}

